'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { supabase } from '@/lib/supabase';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Loader2 } from 'lucide-react';

interface ContentFormProps {
  type: 'industry' | 'solution' | 'achievement';
  initialData?: any;
  mode: 'create' | 'edit';
}

export function ContentForm({ type, initialData, mode }: ContentFormProps) {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState(
    initialData || {
      title: '',
      description: '',
      icon: '',
      services: [],
      value: '',
      label: '',
    }
  );

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const table = `${type}s`; // industries, solutions, achievements
      let data = { ...formData };

      // Format services array for industries and solutions
      if (type !== 'achievement' && typeof data.services === 'string') {
        data.services = data.services.split(',').map((s: string) => s.trim());
      }

      if (mode === 'create') {
        const { error } = await supabase.from(table).insert([data]);
        if (error) throw error;
      } else {
        const { error } = await supabase
          .from(table)
          .update(data)
          .eq('id', initialData.id);
        if (error) throw error;
      }

      router.push(`/admin/${table}`);
      router.refresh();
    } catch (error) {
      console.error('Error saving content:', error);
      alert('Error saving content. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleDelete = async () => {
    if (!confirm('Are you sure you want to delete this item?')) return;

    setIsLoading(true);
    try {
      const { error } = await supabase
        .from(`${type}s`)
        .delete()
        .eq('id', initialData.id);
      
      if (error) throw error;
      
      router.push(`/admin/${type}s`);
      router.refresh();
    } catch (error) {
      console.error('Error deleting content:', error);
      alert('Error deleting content. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {type !== 'achievement' && (
        <>
          <div>
            <Label htmlFor="title">Title</Label>
            <Input
              id="title"
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
              required
            />
          </div>

          <div>
            <Label htmlFor="description">Description</Label>
            <Textarea
              id="description"
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              required
            />
          </div>

          <div>
            <Label htmlFor="icon">Icon</Label>
            <Input
              id="icon"
              value={formData.icon}
              onChange={(e) => setFormData({ ...formData, icon: e.target.value })}
              required
            />
          </div>

          <div>
            <Label htmlFor="services">Services (comma-separated)</Label>
            <Textarea
              id="services"
              value={Array.isArray(formData.services) ? formData.services.join(', ') : formData.services}
              onChange={(e) => setFormData({ ...formData, services: e.target.value })}
              required
            />
          </div>
        </>
      )}

      {type === 'achievement' && (
        <>
          <div>
            <Label htmlFor="value">Value</Label>
            <Input
              id="value"
              value={formData.value}
              onChange={(e) => setFormData({ ...formData, value: e.target.value })}
              required
            />
          </div>

          <div>
            <Label htmlFor="label">Label</Label>
            <Input
              id="label"
              value={formData.label}
              onChange={(e) => setFormData({ ...formData, label: e.target.value })}
              required
            />
          </div>

          <div>
            <Label htmlFor="description">Description</Label>
            <Input
              id="description"
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              required
            />
          </div>
        </>
      )}

      <div className="flex justify-between">
        <div className="space-x-4">
          <Button type="submit" disabled={isLoading}>
            {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            {mode === 'create' ? 'Create' : 'Update'}
          </Button>
          {mode === 'edit' && (
            <Button
              type="button"
              variant="destructive"
              onClick={handleDelete}
              disabled={isLoading}
            >
              Delete
            </Button>
          )}
        </div>
        <Button
          type="button"
          variant="outline"
          onClick={() => router.back()}
          disabled={isLoading}
        >
          Cancel
        </Button>
      </div>
    </form>
  );
} 